.topWrapper {
  min-height: 100vh;
  color: white;
  background-color: black;
  background-size: cover;
}

img {
  margin-right: 10px;
}

.iconChange p {
  font-size: 20px;
}

.iconChange input {
  margin-right: 10px;
  vertical-align: middle;
}

.messageForm img {
  vertical-align: middle;
}

.messageForm form {
  display: inline;
  vertical-align: middle;
}

.messageForm textarea {
  height: 70px;
  width: 350px;
  max-width: 70%;
}

.chatForm img {
  vertical-align: middle;
}

.messageBoxGirl {
  position: relative;
  display: inline-block;
  margin: 1.5em 0 1.5em 15px;
  padding: 7px 10px;
  min-width: 350px;
  max-width: 70%;
  font-size: 16px;
  vertical-align: middle;
  background-color: rgba(187, 14, 58, 0.986);
  border-radius: 10px;
}

